body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.banner-card{
  width: 100vw;
  margin-top: 1vh;
  margin-bottom: 3vh;
  position: relative;
  height: 70hv;
  color: white;
}

.banner-img{
  height: 70vh;
  width: 100%;

}

.banner-title{
  position: absolute;
  top: 42vh;
  left: 5vw;
  
}

.banner-text{
  position: absolute;
  top: 52vh;
  left: 4vw;
  height: 4.5rem;
  overflow: hidden;
  margin-right: 5vw;

}

.movies-list{
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;
  justify-content: center;
}
.movies-card{
  width: 20vw;
  margin: 1rem;
  color: white;
  position: relative;
  
}
.movies-title{
position: absolute;
top: 1rem;
left: 1rem;
}

.movies-button{
  position: absolute;
  bottom: 2rem;
  
}
.movies-img{
  height: 40vh;
  width:20vw ;
}

@media screen and (max-width:415px) {
  .banner-card{
    height:30vh;
  }
  .banner-img{
    height: 30vh;
  }
  .banner-title{
    top:12vh;
    left:5vw;
  }
  .banner-text{
    top:17vh;
    left:5vw
  }

  .movies-card{
    width: 40vw;
    margin:0.3rem;        
  }
  .movies-button{
    width: 80%;
  }
  .movies-img{
    width: 40vw;
    
  }
}

.favourite-genres{
  padding: 3rem;
}

.favourite-table{
  padding: 3rem;
}
@media screen and (min-width: 430px) and (max-width:770px) {
  .movies-card{
    width: 45vw;
    margin:0.3rem;        
  }

  .movies-button{
    width: 80%;
  }
  .movies-img{
    width: 45vw;
    
  }
}
